@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Style Start here */

body {
  margin: 0px;
  padding: 0px;
  background-image: url(/football-team-page/static/media/b4.15a0c356.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 1;
  /* font-family: "Russo One", sans-serif; */
}
.carousel {
  width: 100vw;
  height: 60vh;
  margin-top: 0px;
  margin-bottom: 20px;
  background-color: rgb(8, 8, 8);
  opacity: 0.9;
}
.img {
  height: 60vh;
  opacity: 0.7;
}
.title {
  width: 100vw;
  height: 90px;
  font-size: 2.4rem;
  text-transform: uppercase;
  text-align: center;
  color: #fa8212;
  background-image: url(/football-team-page/static/media/c9.09a5884f.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0.8;
  font-family: "Russo One", sans-serif;
}

.main {
  width: 100vw;
  height: 100%;
  background-image: url(/football-team-page/static/media/p3.b1e3af3c.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 1;
  font-family: "Russo One", sans-serif;
}
.teamContainer,
.playerContainer {
  height: 100%;
  margin-top: 10px;
  opacity: 0.8;
  /* cursor: pointer; */
}

.headerTeam {
  width: 65%;
  height: 60px;
  font-size: 2.2rem;
  color: rgb(185, 245, 21);
  text-align: center;
  background-image: url(/football-team-page/static/media/c5.ab1b3659.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 30px;
  border: 3px solid rgb(185, 245, 21);
}
.headerPlayers {
  width: 65%;
  height: 60px;
  font-size: 2.2rem;
  color: rgb(245, 92, 21);
  text-align: center;
  background-image: url(/football-team-page/static/media/c5.ab1b3659.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 30px;
  border: 3px solid rgb(245, 92, 21);
  animation: choose 1s linear 2;
}
.formC {
  height: 50px !important;
  border: none !important;
  border-radius: 25px !important;
}
.btn {
  border: none !important;
  border-radius: 20px !important;
  background-color: rgb(245, 92, 21) !important;
}

.btn:focus {
  visibility: hidden !important;
}
.myPlayers {
  height: 80px;
  font-size: 1.1rem;
  color: rgb(185, 245, 21);
  margin: 7px;
  background-color: rgb(29, 41, 47);
  border: 2px solid rgb(185, 245, 21);
  border-radius: 30px;
  opacity: 0.9;
  cursor: pointer;
  animation: player 1.5s linear 1;
}

.playersMarket {
  height: 80px;
  font-size: 1.1rem;
  color: rgb(245, 92, 21);
  margin: 7px;
  background-color: rgb(26, 42, 78);
  border: 2px solid rgb(245, 92, 21);
  border-radius: 30px;
  opacity: 0.9;
}

.iconTeam,
.iconPlayers {
  width: 40px;
  height: 40px;
  margin: 7px;
  font-weight: bold !important;
  border: none !important;
  border-radius: 50px !important;
}
ul {
  cursor: pointer;
}

.iconPlayers {
  background-image: url(/football-team-page/static/media/football-157930_1280.dfcad029.png);
  background-size: cover;
  background-repeat: no-repeat;
  border: none;
  animation: spin 3s linear infinite;
}
@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes choose {
  100% {
    transform: rotateX(360deg);
  }
}
@keyframes player {
  100% {
    transform: rotateY(360deg);
  }
}

